<template>
  <div class="NewSpBox">
    <h2 class="SPtitle">新建商品</h2>
    <ul class="SPcont">
      <li>
          <Row>
          <Col span="12">
            <span class="intit">
              仓库编号
            </span>
            <Select v-model="params.storeCode" clearable style="width: 60%" filterable @on-change="sel">
              <Option v-for="(item,index) in cityList" :value="item.storeCode" :key="index">{{ item.storeName }}</Option>
            </Select>
          </Col>
          <Col span="12">
          <span class="intit">
            商品编号
          </span>
            <Input v-model.trim="params.productCode" placeholder="请输入商品编号..." style="width: 60%"></Input>
          </Col>
        </Row>
      </li>
      <li>
        <Row>
          <Col span="12">
            <span class="intit">
              商品名称
            </span>
            <Input v-model.trim="params.productName" placeholder="请输入商品名称..." style="width: 60%"></Input>
          </Col>
          <Col span="12">
            <span class="intit">
              库存个数
            </span>
            <Input v-model.trim.lazy="params.inventory" @on-blur="reg(params.inventory)" placeholder="请输入库存个数..." style="width: 60%"></Input>
          </Col>
        </Row>
      </li>
    </ul>
    <footer class="SPfooter">
      <Row>
        <Col span="12">
          <Button type="primary" :loading="loading2" icon="checkmark-round" @click="toLoading2(params)" style="float: right;margin-right: 100px">
            <span v-if="!loading2">保存</span>
            <span v-else>Loading...</span>
          </Button>
        </Col>
        <Col span="12">
          <Button type="ghost"  icon="checkmark-round" @click="go">
            <span>取消</span>
          </Button>
        </Col>
      </Row>
    </footer>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'newshangpin',
  data () {
    return {
      value: '',
      loading: false,
      loading2: false,
      cityList: [],
      params: {
        inventory: '',
        productCode: '',
        productName: '',
        storeCode: '',
        storeName: ''
      }
    }
  },
  methods: {
    toLoading2 (params) {
      if (this.yanzhen(params)) {
        this.loading2 = true
        var scope = this
        axios.request({
          url: scope.baseUrl + '/product',
          data: params,
          method: 'post'
        }).then(function (res) {
          if (res.data.suc) {
            scope.params = {
              inventory: '',
              productCode: '',
              productName: '',
              storeCode: '',
              storeName: ''
            }
            scope.$Message.success({
              content: res.data.msg,
              duration: 3,
              onClose: function () {
                scope.loading2 = false
                scope.$router.push({name: 'home'})
              }
            })
          } else {
            scope.$Message.error({
              content: res.data.msg,
              duration: 3,
              onClose: function () {
                scope.loading2 = false
              }
            })
          }
        })
      }
    },
    go () {
      this.$router.push({name: 'cangkuguanli'})
    },
    init () {
      var scope = this
      console.log(scope)
      axios.get(scope.baseUrl + '/store')
        .then(function (response) {
          if (response.data.suc) {
            scope.cityList = response.data.obj
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    yanzhen (params) {
      if (params.storeCode == '') {
        this.$Message.warning('请选择仓库编码')
        return false
      } else if (params.storeName == '') {
        this.$Message.warning('请仓库名称')
        return false
      } else if (params.productCode == '') {
        this.$Message.warning('请填写商品编码')
        return false
      } else if (params.productName == '') {
        this.$Message.warning('请填写商品名称')
        return false
      } else if (params.inventory == '') {
        this.$Message.warning('请填写库存数量')
        return false
      }
      return true
    },
    sel (v) {
      for (var i = 0; i < this.cityList.length; i++) {
        if (this.cityList[i].storeCode == v) {
          this.params.storeName = this.cityList[i].storeName
        }
      }
    },
    reg (num) {
      const reg = new RegExp('^[1-9]\\d*|0$')
      if (num != '') {
        if (reg.test(num)) {
          if (parseInt(num) > 99999) {
            this.$Message.warning('请输入0~99999的数值')
          }
        } else {
          this.$Message.warning('请输入数字')
        }
      }
    }
  },
  computed: {},
  mounted () {
    this.init()
  }
}
</script>

